.carteira-sec {
    padding-top: 120px;

    @media screen and (max-width: $bp-LG){
        padding-top: 50px;
    }

    @media screen and (max-width: $bp-MD){
        padding-top: 30px;
    }

    .sub-header {
        margin-bottom: 75px;

        @media screen and (max-width: $bp-LG){
            margin-bottom: 35px;
        }
    }

    &__buttons {
        &-item {
            background: none;
            border: none;
            @include transition();
            &:hover {
                @include scale(1.05);
            }

            img {
                width: 97%;
                max-width: 380px;
                margin: 0 auto;
            }
        }
    }

    &__carousel {
        position: relative;
        padding: 0 100px;
        margin-top: 55.4px;

        @media screen and (max-width: $bp-MD){
           padding: 0 40px;
           margin-top: 20px;
        }

        &-arrows {

            .next,
            .prev {
                position: absolute;
                top: 46%;
                background: none;
                border: none;
                color: $c-education-primary;
                font-size: 39px;
                @include transition();

                @media screen and (max-width: $bp-XL){
                    font-size: 35px;
                }

                @media screen and (max-width: $bp-MD){
                    font-size: 20px;
                }

                &:hover{
                    @include scale(1.1);
                }

                &:hover, &:focus, &:active {
                   outline: none;
                }
            }

            .prev {
                left: 35px;

                @media screen and (max-width: $bp-LG){
                    left: 0;
                }
            }

            .next {
                left: unset;
                right: 0;
            }

        }

        &-item{
            display: none;
            animation: fadeIn .4s;

            &[data-visible="true"]{
                display: block;
            }

            .banner-img {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
    
                img {
                    width: 68.5%;

                    @media screen and (max-width: $bp-LG){
                        max-width: 200px;
                        margin: 0 auto;
                    }

                    @media screen and (max-width: $bp-MD){
                        width: 65%;
                        max-height: 130px;
                        object-fit: contain;
                    }
                }
            }
    
            .caption {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                height: 100%;
                padding-top: 50px;
    
                .title {
                    font-size: 50px;
                    color: $c-education-primary;
                    font-weight: 700;

                    @media screen and (max-width: $bp-XL){
                        font-size: 40px;
                    }

                    @media screen and (max-width: $bp-LG){
                        display: none;
                    }
                }
    
                .desc {
                    font-size: 30px;
                    color: $color-dark;
                    line-height: 1.6;
                    text-align: center;

                    @media screen and (max-width: $bp-XL){
                        font-size: 25px;
                    }

                    @media screen and (max-width: $bp-LG){
                        font-size: 21px;
                        padding-left: 10px;
                        margin-top: -25px;
                    }

                    @media screen and (max-width: $bp-MD){
                        font-size: 15px;
                        padding-left: 0;
                        line-height: 1.24;
                    }
                }
            }
        }
    }

}
